import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'

import articleIndex from '@/view/article/article-index.vue'; //文章首页


import moment from 'moment'
Vue.filter('dateFormat', function (value, type) { //时间格式化过滤
  return moment(value).format(type);
});


Vue.use(Router)

const router = new Router({
  // base: '/article-index.html',
  routes: [{
    path: '/',
    name: 'articleIndex',
    component: articleIndex
  }]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



