// src/view/article/article-index.vue  //文章首页
<template>
   <div>
      <div class="container">
          <ul class="articleHomeList">
                <li v-for="(item,index) in resData.articleHomeList" :key="index" @click="enterArticle(item.id)"> 
                    <!-- 顶部信息 -->
                    <div class="authorInfo">
                        <div class="left">
                            <img :src="item.authorIcon">
                            <span>{{item.author}}</span>
                        </div>
                        <div class="right">
                            {{item.time}}
                        </div>
                    </div>
                    <!-- 标题 -->
                    <p class="authorTitle">{{item.articleTitile}}</p>
                    <!-- 图片 -->
                    <ol class="articleImgs">
                        <li v-for="(items,indexs) in item.imgUrls" :key="indexs">
                            <img :src="items">
                        </li>
                    </ol>
    
                    <!-- 标签 -->
                    <ol class="tags">
                        <li v-for="(tag,i) in item.tags" :key="i"> # {{tag}}</li>
                    </ol>
    
                    <!-- 喜欢及评论数 -->
                    <!-- icon-in_like_border_icon || icon-in_like_icon -->
                    <div class="likeAndcomment">
                        <div>
                            <span :class="item.isLike ? 'icon-in_like_icon' : 'icon-in_like_border_icon'"></span>
                            <span>{{item.likes}}</span>
                        </div>
                        <div>
                            <span class="icon-in_comments_border_icon"></span>
                            <span>{{item.comments}}</span>
                        </div>
                    </div>
    
                </li>
                
          </ul>
      </div>

       <!-- loading -->
      <div :class="this.isLoading?'showLoad':'hideLoad'" class="shade"  >
         <img class="loading" src="../../assets/svg/Spinner-1s-200px.svg">
      </div>
   </div>
</template>

<script>
import appMixins from "@/assets/js/appMixins.js";
export default {
  name: "article-index",
  mixins: [appMixins],
  components: {},
  data() {
    return {
      isLoading: false,
      resData: {}
    };
  },
  methods: {
    enterArticle(id) {
      alert(id);
    },
    getData(data) {
      this.resData = data;
    },
    openLoad() {
      this.isLoading = true;
    },
    closeLoad() {
      this.isLoading = false;
    }
  },
  mounted() {},
  created() {
    let _this = this;
    const resData = {
      articleHomeList: [
        {
          authorIcon:
            "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3744872283,1870099043&fm=27&gp=0.jpg",
          author: "大师兄",
          articleTitile: "华为 Mate 10 保时捷设计版开箱体验",
          time: "1小时前",
          imgUrls: [
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg"
          ],
          tags: ["手机", "华为", "保时捷"],
          isLike: true,
          likes: 12,
          comments: 15,
          id: 110
        },
        {
          authorIcon:
            "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3744872283,1870099043&fm=27&gp=0.jpg",
          author: "二师兄",
          articleTitile: "你任何为人称道的美丽，不及快门一瞬间的你",
          time: "2小时前",
          imgUrls: [
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg"
          ],
          tags: ["冬天", "摄影"],
          isLike: false,
          likes: 12,
          comments: 15,
          id: 120
        }
      ]
    };
    this.getData(resData);
    // this.openLoad();
    // setTimeout(() => {
    //   _this.closeLoad();
    // }, 3000);
  }
};
</script>
<style lang='less' scoped>
@m: 750/375;
.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  border: 1px * @m solid transparent;
  font-size: 14px * @m;
  margin-top: 30px * @m;
  background: #ffffff;
  height: auto;
}

.articleHomeList {
  width: 100%;
}

.articleHomeList li {
  width: 100%;
  /* height: 262px * @m; */
  padding: 16px * @m;
  background: #ffffff;
  box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.5);
  border-radius: 6px * @m;
  margin-bottom: 20px * @m;
  box-sizing: border-box;
}

.articleHomeList li .authorInfo {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  height: 30px * @m;
  margin-bottom: 10px * @m;
}

.authorInfo .left {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
}

.authorInfo .left img {
  display: block;
  width: 30px * @m;
  height: 30px * @m;
  margin-right: 10px * @m;
  border-radius: 100%;
}

.authorInfo .right {
  font-size: 12px * @m;
  color: #c6c6c6;
}

.authorTitle {
  font-size: 16px * @m;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
  margin-bottom: 10px * @m;
}

.articleImgs {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-bottom: 12px * @m;
}

.articleImgs li {
  padding: 0;
  width: 94px * @m;
  height: 94px * @m;
  border-radius: 8px * @m;
  box-sizing: border-box;
  overflow: hidden;
  margin-bottom: 0px * @m;
}

.articleImgs li img {
  display: inline-block;
  width: 100%;
  height: 100%;
}

.tags {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  margin-bottom: 12px * @m;
}

.tags li {
  box-sizing: border-box;
  background-color: #f5f7f9;
  padding: 5px * @m 9px * @m;
  height: 22px * @m;
  margin-right: 10px * @m;
  font-size: 12px * @m;
  color: #9bc2d9;
  width: auto;
  line-height: 12px * @m;
  margin-bottom: 0px * @m;
}

.likeAndcomment {
  color: #c9d6de;
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
}

.likeAndcomment div {
  margin-right: 24px * @m;
  height: 18px * @m;
  overflow: hidden;
}

.likeAndcomment div span:first-child {
  width: 16px * @m;
  height: 18px * @m;
  margin-right: 3px * @m;
}

.likeAndcomment div span:last-child {
  font-size: 12px * @m;
  vertical-align: top;
}

.icon-in_like_icon {
  color: #ff8888;
}

.lessImg {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  align-items: center;
  margin-bottom: 12px * @m;
}

.lessImg img {
  display: block;
  width: 94px * @m;
  height: 94px * @m;
  border-radius: 8px * @m;
  box-sizing: content-box;
  margin-right: 16px * @m;
}

.lessImg .authorTitle {
  flex: 1;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
}
</style>


